___CSS_LOADER_EXPORT___.push([module.id, `@media only screen and (max-width: 340px) {
  nav {
    display: flex;
    flex-direction: row;
  }

  .navbar-item a,
  .container_subtitle,
  .btn {
    font-size: 1em;
  }

  .font-brand,
  .container_title {
    font-size: 1.5em;
  }
}

@media only screen and (max-width: 600px) {
    nav {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex-wrap: wrap;
    padding: 3em 0;
  }
  .menu {
    display: block;
    min-height: 45px;
    min-width: 45px;
    text-align: center;
    border: 1px solid transparent;
    background-color: transparent;
  }
  .menu span {
    text-align: center;
  }
  .brand {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .navbar-list {
    display: none;
  }
  .navbar-list-block {
    position: absolute;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    border-radius: 2em;
    top: 7em;
    width: 80vw;
    height: 50vh;
    background-color: var(--btn);
  }

  .detail {
    display: grid;
    grid-gap: 15px;
    font-size: 1em;
    margin-top: 1em;
  }

#main-container {
  margin: 0;
}

  .detail-food li,
  .detail-drink li {
    padding: 0.5em 1em;
    text-align: start;
  }

  .img-container {
    max-height: 818px;
  }

  .category {
    display: inline-block;
  }

  .submit-btn {
    padding: 5px;
    font-weight: 500;
    font-size: 14px;
  }
  
}

@media only screen and (min-width: 600px) {

  .content_title {
    margin-bottom: 1em;
  }
  .container_title {
    font-size: xxx-large;
    font-weight: 600;
  }
  #explore {
    display: grid;
    grid-row-gap: 1.545em;
    grid-column-gap: 1.545em;
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (min-width: 768px) {}

@media only screen and (min-width: 992px) {
  #explore,
  #fav-resto {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (min-width: 1200px) {
  main {
    max-width: 1200px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive/_responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;;;IAGE,cAAc;EAChB;;EAEA;;IAEE,gBAAgB;EAClB;AACF;;AAEA;IACI;IACA,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,eAAe;IACf,cAAc;EAChB;EACA;IACE,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,6BAA6B;IAC7B,6BAA6B;EAC/B;EACA;IACE,kBAAkB;EACpB;EACA;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,WAAW;EACb;EACA;IACE,aAAa;EACf;EACA;IACE,kBAAkB;IAClB,aAAa;IACb,6BAA6B;IAC7B,sBAAsB;IACtB,kBAAkB;IAClB,QAAQ;IACR,WAAW;IACX,YAAY;IACZ,4BAA4B;EAC9B;;EAEA;IACE,aAAa;IACb,cAAc;IACd,cAAc;IACd,eAAe;EACjB;;AAEF;EACE,SAAS;AACX;;EAEE;;IAEE,kBAAkB;IAClB,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,YAAY;IACZ,gBAAgB;IAChB,eAAe;EACjB;;AAEF;;AAEA;;EAEE;IACE,kBAAkB;EACpB;EACA;IACE,oBAAoB;IACpB,gBAAgB;EAClB;EACA;IACE,aAAa;IACb,qBAAqB;IACrB,wBAAwB;IACxB,8BAA8B;EAChC;AACF;;AAEA,2CAA2C;;AAE3C;EACE;;IAEE,aAAa;IACb,kCAAkC;EACpC;AACF;;AAEA;EACE;IACE,iBAAiB;EACnB;AACF","sourcesContent":["@media only screen and (max-width: 340px) {\n  nav {\n    display: flex;\n    flex-direction: row;\n  }\n\n  .navbar-item a,\n  .container_subtitle,\n  .btn {\n    font-size: 1em;\n  }\n\n  .font-brand,\n  .container_title {\n    font-size: 1.5em;\n  }\n}\n\n@media only screen and (max-width: 600px) {\n    nav {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    flex-wrap: wrap;\n    padding: 3em 0;\n  }\n  .menu {\n    display: block;\n    min-height: 45px;\n    min-width: 45px;\n    text-align: center;\n    border: 1px solid transparent;\n    background-color: transparent;\n  }\n  .menu span {\n    text-align: center;\n  }\n  .brand {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 100%;\n  }\n  .navbar-list {\n    display: none;\n  }\n  .navbar-list-block {\n    position: absolute;\n    display: flex;\n    justify-content: space-around;\n    flex-direction: column;\n    border-radius: 2em;\n    top