___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: hsl(51, 69%, 87%);
  --secondary-color: #D0D4CA;
  --bg-color: #C7EDE6;
  --btn-hover: #A9B388;
  --btn: #2D81D3;
  --navbar-hover: #D0D4CA;
  --font-color: #555843;
}

* {
  padding: 0;
  margin: 0;
}

.link {
  background-color: var(--btn);
  border-radius: 4px;
  color: var(--bg-color);
  font-weight: 700;
  left: 0;
  line-height: 44px;
  margin: 0 auto;
  padding: 0 10px;
  position: absolute;
  right: 0;
  top: -50px;
  transition: 0.35s;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  z-index: 1500;
}

.link:focus {
  top: 35px;
}

body {
  font-size: 14px;
  font-family: 'Poppins', sans-serif;
  color: var(--font-color);
  margin: 0;
}

body,
html {
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  background-color: var(--primary-color);
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  overflow: visible;
  height: 0;
}

pre {
  font-size: 1em;
  font-family: monospace, monospace;
}

code,
kbd,
samp {
  font-size: 1em;
  font-family: monospace, monospace;
}
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

small {
  font-size: 80%;
}

b,
strong {
  font-weight: bolder;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  vertical-align: baseline;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: 1px dotted ButtonText;
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
  -webkit-appearance: button;
  -moz-appearance: none;
  appearance: none;
}

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

progress {
  vertical-align: baseline;
}

legend {
  display: table;
  box-sizing: border-box;
  white-space: normal;
  color: inherit;
  max-width: 100%;
  padding: 0;
}

textarea {
  overflow: auto;
}

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}

[type='checkbox'],
[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}

[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

[type='search'] {
  -webkit-appearance: textfield;
  -moz-appearance: none;
  appearance: none;
  outline-offset: -2px;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

details {
  display: block;
}

[hidden] {
  display: none;
}
template {
  display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/base/_reset.css"],"names":[],"mappings":"AAAA;EACE,kCAAkC;EAClC,0BAA0B;EAC1B,mBAAmB;EACnB,oBAAoB;EACpB,cAAc;EACd,uBAAuB;EACvB,qBAAqB;AACvB;;AAEA;EACE,UAAU;EACV,SAAS;AACX;;AAEA;EACE,4BAA4B;EAC5B,kBAAkB;EAClB,sBAAsB;EACtB,gBAAgB;EAChB,OAAO;EACP,iBAAiB;EACjB,cAAc;EACd,eAAe;EACf,kBAAkB;EAClB,QAAQ;EACR,UAAU;EACV,iBAAiB;EACjB,0BAA0B;EAC1B,uBAAuB;EACvB,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,eAAe;EACf,kCAAkC;EAClC,wBAAwB;EACxB,SAAS;AACX;;AAEA;;EAEE,WAAW;EACX,aAAa;EACb,UAAU;EACV,SAAS;EACT,uBAAuB;EACvB,sCAAsC;AACxC;;AAEA;EACE,iBAAiB;EACjB,8BAA8B;AAChC;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,uBAAuB;EACvB,iBAAiB;EACjB,SAAS;AACX;;AAEA;EACE,cAAc;EACd,iCAAiC;AACnC;;AAEA;;;EAGE,cAAc;EACd,iCAAiC;AACnC;AACA;EACE,0BAA0B;EAC1B,iCAAiC;EACjC,mBAAmB;AACrB;;AAEA;EACE,cAAc;AAChB;;AAEA;;EAEE,mBAAmB;AACrB;;AAEA;;EAEE,kBAAkB;EAClB,cAAc;EACd,wBAAwB;EACxB,cAAc;AAChB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,WAAW;AACb;;AAEA;;;;EAIE,8BAA8B;AAChC;;AAEA;;;;EAIE,0BAA0B;EAC1B,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;;;;EAIE,kBAAkB;EAClB,UAAU;AACZ;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;EACE,cAAc;EACd,sBAAsB;EACtB,mBAAmB;EACnB,cAAc;EACd,eAAe;EACf,UAAU;AACZ;;AAEA;EACE,cAAc;AAChB;;AAEA;;EAEE,YAAY;AACd;;AAEA;;EAEE,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;EACE,6BAA6B;EAC7B,qBAAqB;EACrB,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA;EACE,0BAA0B;EAC1B,aAAa;AACf;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;AACf;AACA;EACE,aAAa;AACf","sourcesContent":[":root {\n  --primary-color: hsl(51, 69%, 87%);\n  --secondary-color: #D0D4CA;\n  --bg-color: #C7EDE6;\n  --btn-hover: #A9B388;\n  --btn: #2D81D3;\n  --navbar-hover: #D0D4CA;\n  --font-color: #555843;\n}\n\n* {\n  padding: 0;\n  margin: 0;\n}\n\n.link {\n  background-color: var(--btn);\n  border-radius: 4px;\n  color: var(--bg-color);\n  font-weight: 700;\n  left: 0;\n  line-height: 44px;\n  margin: 0 auto;\n  padding: 0 10px;\n  position: absolute;\n  right: 0;\n  top: -50px;\n  transition: