.detail {
  display: grid;
  grid-gap: 1.545em;
  font-size: 1em;
  margin-top: 1em;
}

.detail-info li {
  position: relative;
  display: block;
  padding: 0.6em;
  color: var(--font-color);
  text-decoration: none;
  background-color: var(--primary-color);
}

#main-container {
  background-color: var(--bg-color);
  padding: 0 3em;
  margin: 1em;
}

.img-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-height: 680px;
  transition: transform 0.5s ease;
}

.detail-img {
  width: 100%;
  height: 370px;
  border-top-right-radius: 1em;
    border-top-left-radius: 1em;
  margin: auto;
  object-fit: cover;
  object-position: center;
}

.detail-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 0;
  margin-bottom: 0;
}

.icon-primary {
  color: var(--btn);
}

.detail h3 {
  font-size: 1.5em;
  padding: 0.5em 0;
  text-align: center;
  background-color: var(--primary-color);
  font-weight: bold;
}

.detail-food li,
.detail-drink li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  background-color: var(--primary-color);
  border: 0.5px solid var(--secondary-color);
  border-width: 0 0 0.5px;
}

.category {
  padding: 0.3em 1em;
  color: var(--primary-color);
  background-color: var(--btn);
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 0px 10px 0px 10px;
}

.detail-menu h4 {
  font-weight: bold;
  font-size: 1.37em;
  padding: 1em;
  color: var(--btn);
  background-color: var(--primary-color);
}

.detail-desc {
  text-align: justify;
  color: darkgrey;
  margin-bottom: 0.5em;
  font-style: italic;
}

.detail-food {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-drink {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
}

.detail-name-address-rating {
  display: inline-block;
}

/* DETAIL REVIEW */
.detail-review {
  max-width: 100%;
  font-size: 1em;
  text-align: center;
}

.detail-item-review:last-child {
  margin-bottom: 0;
}

.review-name {
  font-weight: bold;
  display: flex;
  align-items: center;
}

.detail-item-review {
  color: var(--font-color);
  background-color: var(--primary-color);
  margin-bottom: 1.5em;
  padding: 1em 1em;
}

.body-review {
  padding: 1em;
  text-align: left;
}

.header-review {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em;
  color: var(--btn);
  border-bottom: 1px solid var(--font-color);
}

.detail-food li:hover,
.detail-drink li:hover,
.category:hover,
.detail-item-review:hover {
  box-shadow: 0px 10px 13px -7px var(--secondary-color),
    5px 5px 15px 5px var(--secondary-color);
}

.review-date {
  font-size: 0.8em;
  font-weight: lighter;
}

/* FORM */
form {
  margin: 2em 0;
  padding: 1.5em;
  border: 3px dotted var(--btn-hover);
  border-radius: 0 15px 0 15px;
}

.form-label {
  padding-bottom: 10px;
  font-weight: bold;
  font-style: normal;
  font-size: small;
  color: var(--btn);
}

.form-control:hover {
  border-bottom: 1px solid var(--btn);
}

.mb-3 {
  margin-bottom: 1.3em;
  margin-top: 0.5em;
}

.detail-title, 
.title-container {
  text-align: center;
  padding-top: 1em; 
  margin-bottom: 0.5em;
}

.form-control {
  display: block;
  width: 100%;
  min-height: 44px;
  padding: 0.3em;
  font-size: 1em;
  font-weight: normal;
  color: var(--font-color);
  background-color: #fff;
  appearance: none;
  outline: none;
  border: 1px solid transparent;
  border-bottom: 1px solid var(--secondary-color);
}`, "",{"version":3,"sources":["webpack://./src/styles/pages/_detail.css"],"names":[],"mappings":"AAAA,WAAW;AACX;EACE,aAAa;EACb,iBAAiB;EACjB,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,cAAc;EACd,wBAAwB;EACxB,qBAAqB;EACrB,sCAAsC;AACxC;;AAEA;EACE,iCAAiC;EACjC,cAAc;EACd,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,WAAW;EACX,iBAAiB;EACjB,+BAA+B;AACjC;;AAEA;EACE,WAAW;EACX,aAAa;EACb,4BAA4B;IAC1B,2BAA2B;EAC7B,YAAY;EACZ,iBAAiB;EACjB,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,6BAA6B;EAC7B,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,kBAAkB;EAClB,sCAAsC;EACtC,iBAAiB;AACnB;;AAEA;;EAEE,gBAAgB;EAChB,cAAc;EACd,qBAAqB;EACrB,sCAAsC;EACtC,0CAA0C;EAC1C,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,2BAA2B;EAC3B,4BAA4B;EAC5B,iBAAiB;EACjB,6BAA6B;EAC7B,gCAAgC;AAClC;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,YAAY;EACZ,iBAAiB;EACjB,sCAAsC;AACxC;;AAEA;EACE,mBAAmB;EACnB,eAAe;EACf,oBAAoB;EACpB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA,kBAAkB;AAClB;EACE,eAAe;EACf,cAAc;EACd,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,wBAAwB;EACxB,sCAAsC;EACtC,oBAAoB;EACpB,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,gBAAgB;EAChB,iBAAiB;EACjB,0CAA0C;AAC5C;;AAEA;;;;EAIE;2CACyC;AAC3C;;AAEA;EACE,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA,SAAS;AACT;EACE,aAAa;EACb,cAAc;EACd,mCAAmC;EACnC,4BAA4B;AAC9B;;AAEA;EACE,oBAAoB;EACpB,iBAAiB;EACjB,kBAAkB;EAClB,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,oBAAoB;EACpB,iBAAiB;AACnB;;AAEA;;EAEE,kBAAkB;EAClB,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA;EACE,cAAc;EACd,WAAW;EACX,gBAAgB;EAChB,cAAc;EACd,cAAc;EACd,mBAAmB;EACnB,wBAAwB;EACxB,sBAAsB;EACtB,gBAAgB;EAChB,aAAa;EACb,6BAA6B;EAC7B,+CAA+C;AACjD","sourcesContent":["/* DETAIL */\n.detail {\n  display: grid;\n  grid-gap: 1.545em;\n  font-size: 1em;\n  margin-top: 1em;\n}\n\n.detail-inf