___CSS_LOADER_EXPORT___.push([module.id, `.container {
  display: contents !important;
}

.container_text {
  margin: auto 0;
  align-self: center;
  padding-bottom: 3em;
  font-weight: 500;
  max-width: 600px;
  text-shadow: 2px 1px 1px var(--primary-color);
}

.container_title {
  font-size: xx-large;
  font-weight: 500;
  color: var(--bg-color);
}

.container_subtitle {
  margin: 16px;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 18px;
  word-spacing: 2px;
  line-height: 1.36em;
  color: var(--btn);
}

.content_container {
  padding: 1em;
  margin: 2em 10%;
  border-radius: 15px;
  background-color: #D0D4CA;
}

.content_title {
  margin-bottom: 0.5em;
  text-align: center;
}

#explore,
#fav-resto {
  display: grid;
  place-items: center;
  grid-row-gap: 1.5em;
  padding-top: 1.5em;
}

.card {
  width: 90%;
  text-align: center;
  border: 1px solid grey;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: top ease 0.4s;
  box-shadow: 10px 10px 5px 0px var(--secondary-color);
  background-color: var(--bg-color);
}

.card-a-tag:focus {
  outline: 4px dashed orange;
}

.card:hover {
  width: 100%;
}

.card-rating {
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 0 10px 0 0;
  padding: 0.1em 1em;
  color: white;
  font-size: large;
  z-index: 1;
  background-color: var(--font-color);
}

.card-rating .fa {
  padding: 0.3em 0.8em 0.3em 0;
  font-size: smaller;
  color: yellow;
}

.city {
  position: absolute;
  padding: 0.4em 1em;
  top: 0.5em;
  border-radius: 0 0.5em 0.5em 0 ;
  color: white;
  background-color: var(--font-color);
}
.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 13px;
}

.card-content {
  font-size: small;
  text-align: center;
  padding: 1.4em 2em;
  background-color: var(--bg-color);
}

.card-content-title {
  padding-bottom: 0.376em;
  font-weight: bold;
}

.truncate {
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}`, "",{"version":3,"sources":["webpack://./src/styles/pages/_home.css"],"names":[],"mappings":"AAAA;EACE,4BAA4B;AAC9B;;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB,mBAAmB;EACnB,gBAAgB;EAChB,gBAAgB;EAChB,6CAA6C;AAC/C;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;EAChB,sBAAsB;AACxB;;AAEA;EACE,YAAY;EACZ,mBAAmB;EACnB,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,YAAY;EACZ,eAAe;EACf,mBAAmB;EACnB,yBAAyB;AAC3B;;AAEA;EACE,oBAAoB;EACpB,kBAAkB;AACpB;;AAEA;;EAEE,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,UAAU;EACV,kBAAkB;EAClB,sBAAsB;EACtB,mBAAmB;EACnB,gBAAgB;EAChB,eAAe;EACf,yBAAyB;EACzB,oDAAoD;EACpD,iCAAiC;AACnC;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,yBAAyB;EACzB,kBAAkB;EAClB,YAAY;EACZ,gBAAgB;EAChB,UAAU;EACV,mCAAmC;AACrC;;AAEA;EACE,4BAA4B;EAC5B,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,UAAU;EACV,+BAA+B;EAC/B,YAAY;EACZ,mCAAmC;AACrC;AACA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,kBAAkB;EAClB,iCAAiC;AACnC;;AAEA;EACE,uBAAuB;EACvB,iBAAiB;AACnB;;AAEA;EACE,oBAAoB;EACpB,uBAAuB;EACvB,gBAAgB;EAChB,qBAAqB;EACrB,4BAA4B;AAC9B","sourcesContent":[".container {\n  display: contents !important;\n}\n\n.container_text {\n  margin: auto 0;\n  align-self: center;\n  padding-bottom: 3em;\n  font-weight: 500;\n  max-width: 600px;\n  text-shadow: 2px 1px 1px var(--primary-color);\n}\n\n.container_title {\n  font-size: xx-large;\n  font-weight: 500;\n  color: var(--bg-color);\n}\n\n.container_subtitle {\n  margin: 16px;\n  margin-bottom: 30px;\n  font-weight: 500;\n  font-size: 18px;\n  word-spacing: 2px;\n  line-height: 1.36em;\n  color: var(--btn);\n}\n\n.content_container {\n  padding: 1em;\n  margin: 2em 10%;\n  border-radius: 15px;\n  background-color: #D0D4CA;\n}\n\n.content_title {\n  margin-bottom: 0.5em;\n  text-align: center;\n}\n\n#explore,\n#fav-resto {\n  display: grid;\n  place-items: center;\n  grid-row-gap: 1.5em;\n  padding-top: 1.5em;\n}\n\n.card {\n  w